<!-- 文章编辑与添加 -->
<template>
	<div class="article-edit">
		<div class="flex">
			<div style="padding-left: 5%;width: 440px;">
				<div class="add-article-select">
		    		<span class="title">发布时间</span>
		    		<el-date-picker v-model="date" type="datetime" placeholder="选择日期时间" align="center"></el-date-picker>
					<span class="required">*</span>
		    	</div>
		    	<div class="add-article-select">
					<span class="title">文章分类</span>
					<el-cascader :options="articleList" v-model="selectedOptions" change-on-select></el-cascader>
					<span class="required">*</span>
		    	</div>
		    	<div class="add-article">
		    		<span class="title">文章标题</span>
		    		<el-input placeholder="请输入文章标题" v-model="articleName" clearable></el-input>
		    		<span class="required">*</span>
		    	</div>
		    	<div class="add-article">
		    		<span class="title">文章标签</span>
		    		<el-input placeholder="请输入文章标签" v-model="articleTag" clearable></el-input>
		    	</div>
		    	<div class="add-article">
		    		<span class="title" style="vertical-align: top;">文章摘要</span>
		    		<el-input type="textarea" placeholder="请输入文章摘要" v-model="articleExcerpt" clearable></el-input>
		    	</div>
		    	<div class="add-article">
		    		<span class="title">是否案例</span>
		    		<el-radio v-model="radio" :label="true">是</el-radio>
  					<el-radio v-model="radio" :label="false">否</el-radio>
		    	</div>
			</div>
			<div style="width: 500px;">
				<div class="img-upload flex">
		    		<div style="margin-right: 15px;"><span class="title">缩略图</span></div>
		    		<div class="preview-img" v-if="base64 != ''">
						<img :src="base64" class="slide-add-title">
						<el-button @click="destoryFile()" v-if="base64 != ''" style="margin-top: 15px;">取消图片</el-button>
					</div>
					<div>
						<div class="el-upload el-upload--picture-card" v-if="base64 == ''">
							<i class="el-icon-plus"></i>
							<input type="file" @change="fileImage" accept="image/jpeg,image/x-png,image/gif">
						</div>
					</div>
		    	</div>
			</div>
		</div>
		<div style="padding-left: 5%;">
	    	<el-collapse-transition>
		    	<div v-show="radio" class="flex" style="flex-wrap: wrap;">
		    		<div class="add-article">
			    		<span class="title">姓名</span>
			    		<el-input placeholder="请输入姓名" v-model="caseName" clearable></el-input>
			    	</div>
			    	<div class="add-article">
			    		<span class="title">年龄</span>
			    		<el-input placeholder="请输入年龄" v-model="caseAge" clearable></el-input>
			    	</div>
			    	<div class="add-article">
			    		<span class="title">症状</span>
			    		<el-input placeholder="请输入症状" v-model="caseSymptom" clearable></el-input>
			    	</div>
			    	<div class="add-article">
			    		<span class="title">方案</span>
			    		<el-input placeholder="请输入方案" v-model="casePlan" clearable></el-input>
			    	</div>
		    	</div>
		    </el-collapse-transition>
		</div>
		<div class="add-article" style="padding-left: 5%;">
			<span class="title" style="vertical-align: top;">文章内容</span>
			<div id="editorElem" class="editorElem" style="text-align:left"></div>
		</div>
		<div class="add-article" style="padding-left: 5%;">
			<el-button type="primary" @click="addOrEdit()">{{type == 'add' ? '添加' : '保存'}}</el-button>
			<el-button @click="goBack()">返回</el-button>
		</div>
	</div>
</template>

<script>
	import apis from '../../apis'
	import E from 'wangeditor'

	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				articleList: [],
				selectedOptions: [],
				articleName: '',
				articleTag: '',
				articleExcerpt: '',
				editorContent: '',
				date: '',
				base64: '',
				parentId: [],
				articleId: 0,
				radio: false,
				caseName: '',
				caseAge: '',
				caseSymptom: '',
				casePlan: '',
				type: '',
			}
		},
		methods: {
			// 添加 & 修改
			addOrEdit() {
				var more = null;
				if(this.radio) {
					if(this.caseName == '') {
						this.$alert('请输入名字');
					}
					else if(Math.floor(this.caseAge) != this.caseAge) {
						this.$alert('请输入年龄');
					}
					else {
						more = {'name': this.caseName, 'age': this.caseAge, 'symptom': this.caseSymptom, 'plan': this.casePlan};
					}
				}
				if(this.type == 'add'){
					apis.articleAdd({post_title: this.articleName, post_tag: this.articleTag, post_excerpt: this.articleExcerpt, post_content: this.editorContent, category_id: this.selectedOptions[this.selectedOptions.length-1], published_time: this.date, thumb: this.base64, more: more, token: this.$root.token}, function(bck) {
						this.$alert(bck.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	if(bck.info == '您还没有登录！！') {
				            		location.reload();
				            	}
				            	else if(bck.code == 1) {
				            		this.reload();
				            	}
				          	}
				        });
					}.bind(this))
				}
				else if(this.type == 'edit') {
					apis.articleEdit({id: this.articleId, post_title: this.articleName, category_id: this.selectedOptions[this.selectedOptions.length-1], post_tag: this.articleTag, post_excerpt: this.articleExcerpt, post_content: this.editorContent, published_time: this.date, thumb: this.base64, more: more, token: this.$root.token}, function(bck) {
						this.$alert(bck.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	if(bck.info == '您还没有登录！！') {
				            		location.reload();
				            	}
				            	else if(bck.code == 1) {
				            		this.reload();
				            	}
				          	}
				        });
					}.bind(this))
				}
			},
			// 上传图片
		    fileImage:function(e) {
		    	var self = this;
                var file = e.target.files[0];
                var reader = new FileReader();
                reader.readAsDataURL(file); // base64
                reader.onloadend = function () {
                    // 图片的 base64 格式
                    self.base64 = reader.result;
                };
            },
            // 取消图片
            destoryFile() {
            	this.base64 = '';
            },
            // 返回
            goBack() {
            	this.$emit("change", "first");
            }
		},
		created() {
			this.articleList = this.$root.propList;
			this.type = this.$root.propType;
			if(this.type == 'add') {
				this.date = new Date();
			}
			else if(this.type == 'edit') {
				var editArticle = this.$root.editArticle;
				this.date = editArticle.published_time;
				this.articleName = editArticle.post_title;
				this.parentId = editArticle.parents;
				this.parentId = this.parentId.split(',');
				this.selectedOptions = this.parentId.map(Number);
				this.editorContent = editArticle.post_content;
				this.articleId = editArticle.id;
				this.articleTag = editArticle.post_tag;
				this.articleExcerpt = editArticle.post_excerpt;
				if(editArticle.thumb) {
					this.base64 = editArticle.thumb;
				}
				if(editArticle.more === null) {
					this.radio = false;
				}
				else {
					this.radio = true;
					this.caseName = JSON.parse(editArticle.more).name;
					this.caseAge = JSON.parse(editArticle.more).age;
					this.caseSymptom = JSON.parse(editArticle.more).symptom;
					this.casePlan = JSON.parse(editArticle.more).plan;
				}
			}
		},
     	mounted() {
     		// 初始化富文本编辑器
	        var editor = new E('#editorElem');
	        var self = this;
			editor.customConfig.uploadImgShowBase64 = true;   // 使用 base64 保存图片
          	// 自定义配置颜色（字体颜色、背景色）
		    editor.customConfig.colors = [
		        '#000000', '#eeece0', '#1c487f', '#4d80bf', '#ff0000', '#8baa4a', '#7b5ba1', '#46acc8', '#f9963b', '#ffffff'
		    ]
		    // 自定义菜单配置
		    editor.customConfig.menus = [
		        'head',  // 标题
			    'bold',  // 粗体
			    'fontSize',  // 字号
			    'fontName',  // 字体
			    'italic',  // 斜体
			    'underline',  // 下划线
			    'strikeThrough',  // 删除线
			    'foreColor',  // 文字颜色
			    'backColor',  // 背景颜色
			    'link',  // 插入链接
			    'list',  // 列表
			    'justify',  // 对齐方式
			    'quote',  // 引用
			    'image',  // 插入图片
			    'table',  // 表格
			    'video',  // 插入视频
			    'undo',  // 撤销
		    ]
	        editor.customConfig.onchange = (html) => {
	          this.editorContent = html;
	        }
	        editor.customConfig.debug = true;
	        editor.create();
	        editor.txt.html(this.editorContent);
      	}
	}
</script>

<style lang="less">
	.article-edit {
		.el-radio__label {
			vertical-align: middle;
		}
		.img-upload {
			margin-top: 20px;
			.preview-img {
	        	width: 80%;
	        	img {
	        		width: 100%;
	        	}
	        }
			.el-upload {
			    position: relative;
			    line-height: 148px;
			}
			.el-upload input {
			    position: absolute;
			    right: 0;
			    top: 0;
			    opacity: 0;
			    cursor: pointer;
	            width: 100%;
    			height: 100%;
			}
		}
		.add-article, .add-article-select {
			margin-top: 20px;
			.title {
				margin-right: 15px;
				width: 75px;
				text-align: right;
				display: inline-block;
			}
			.el-input, .el-textarea {
				width: 217px;
			}
			.editorElem {
			    width: 75%;
    			display: inline-block;
			}
		}
	}
	.w-e-text-container {
		z-index: 0 !important;
	}
	.el-date-picker {
		z-index: 20000 !important;
	}
	.el-message-box__wrapper {
		z-index: 20009 !important;
	}
</style>


